import '@/util/jquery.js'
class Service {
  Get(api,reqbody){
    console.log(api,reqbody)
    return new Promise(function (resolve, reject) {
      $.ajax({
          url:'http://tingapi.ting.baidu.com/v1/restserver/ting?method=baidu.ting.song.downWeb&songid=877578&bit=24&_t=1393123213'+api,
          type:'GET', //GET
          async:true,    //false,
          data:reqbody,
          timeout:5000,    //
          dataType:'json',    //json/xml/html/script/jsonp/text
          beforeSend:function(xhr){
              console.log(xhr)
              console.log('')
          },
          success:function(data,textStatus,jqXHR){
            //   console.log(data)
            //   console.log(textStatus)
            //   console.log(jqXHR)
              resolve(data)
          },
          error:function(xhr,textStatus){
              console.log('')
            //   console.log(xhr)
            //   console.log(textStatus)
              reject(textStatus)
          },
          complete:function(){
              console.log('')
          }
      })
    })
  }
  Post(api,reqbody){
    console.log(api,reqbody)
    return new Promise(function (resolve, reject) {
      let msg = {
        "reqBody":  reqbody
      }
      let reqData = 'reqContent='+encodeURI(JSON.stringify(msg));
      $.ajax({
          url:'http://localhost:2016/'+api,
          type:'POST', //GET
          async:true,    //false,
          data:reqData,
          timeout:5000,    //
          dataType:'json',    //json/xml/html/script/jsonp/text
          beforeSend:function(xhr){
            //   console.log(xhr)
              console.log('')
          },
          success:function(data,textStatus,jqXHR){
            //   console.log(data)
            //   console.log(textStatus)
            //   console.log(jqXHR)
              resolve(data)
          },
          error:function(xhr,textStatus){
            //   console.log('')
            //   console.log(xhr)
            //   console.log(textStatus)
              reject(textStatus)
          },
          complete:function(){
              console.log('')
          }
      })
    })
  }
}
export default Service= new Service



